.content {
    background-image: url('../images/headerbackground.jpg');  
    width: 100%;
    height: 40rem;
    display: flex;
    justify-content: center;
}

.space{
    display: flex;
    width: 50%;
    max-width: 25rem;
}

.content p{
    margin: 0;
    color: white;
    font-size: 60px;
}
.content h1{
    color: black;
    font-size: 16px;
    font-weight: 400;
    color: white;
    
}
.letterwidth{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: auto;
    
}
.letterwidth button{
    padding: 1rem 2rem;
    background-color: rgb(247, 62, 62);
    color: white;
    border: none;      
    margin-top: 1rem;  
}
@media (max-width: 658px){
    .content{
        display: flex;        
        
    }
    .letterwidth{
        min-width: 23rem;
        text-align: center;
    }
    .space{
        display: flex;
        width: 50%;
    }
    .john{
        display: flex;
       
    }
    .john img{
        margin-top: 35rem;
        width: 100%;
    }
    .emptydiv{
        margin-top: 2rem;
        
    }
    .secondpara{       
        width: 50%;
    }
}
.john{
    width: 50%;
    display: flex;
    margin-top: 20rem;
    justify-content: center;
    align-items: center;    
}
.john img{
    max-height: 40rem;
}
.para{
    width: 60%;
}
.para h1{
    font-size: 40px;
    font-weight: normal;
}
.secondpara{ 
    padding: 0 10%;
}
.empty{
    margin-top: -7rem;
    
}
.emptydiv{
    width: 1.5%;
    height: 100px;
    background-color: rgb(244, 57, 57);
    padding: 3% 0;
}
