
.finalText{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    margin: 3rem;
}
@media (max-width: 658px) {
    .finalText{
        align-items: normal;
        flex-direction: column;
        margin: 0rem 1rem;
    }   
    .final1{        
        margin-bottom: 2rem;
        margin-left: 0;
    }
    .final1 img{
        height: 100%;
        width: 50%;
    }
   
    .final1 h1{
        letter-spacing: 2px;
        font-size: 60px;
        font-weight: lighter;
        
    }
}
.textwid{
    margin-left: 1rem;
    margin-right: 1rem;
}
.emptyfinal{
    width: 100%;
    height: 8px;
    background-color: brown;
    margin-top: 2rem;
}

.final1 img{
    height: 100%;
    width: 100%;
}